:root {
  --background: #0a0a0a;
  --foreground: #ededed;
  --color-background: var(--background);
  --color-foreground: var(--foreground);
  --color-primary: #ff950e;
  --color-primary-dark: #e0850d;
  --color-primary-light: #ffb347;
  --color-card: #171717;
  --font-sans: var(--font-geist-sans, Arial, Helvetica, sans-serif);
  --font-mono: var(--font-geist-mono, monospace);
}
body {
  background: var(--color-background);
  color: var(--color-foreground);
  font-family: var(--font-sans);
}
a {
  color: var(--color-primary);
  text-decoration: none;
}
a:hover {
  color: var(--color-primary-light);
}
.card {
  background: var(--color-card);
  border-radius: 1rem;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,0.12);
  padding: 1.5rem;
  color: var(--color-foreground);
}
.custom-scrollbar::-webkit-scrollbar {
  width: 8px;
}
.custom-scrollbar::-webkit-scrollbar-track {
  background: #1a1a1a;
}
.custom-scrollbar::-webkit-scrollbar-thumb {
  background: #444;
  border-radius: 4px;
}
.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #555;
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 149, 14, 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(255, 149, 14, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 149, 14, 0);
  }
}
.verification-badge {
  animation: pulse 2s infinite;
}
.glassmorphism {
  background: rgba(10, 10, 10, 0.7);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.05);
}
@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.animated-gradient {
  background: linear-gradient(-45deg, #0a0a0a, #121212, #1a1a1a, #222);
  background-size: 400% 400%;
  animation: gradientAnimation 15s ease infinite;
}
.img-zoom-container {
  overflow: hidden;
}
.img-zoom {
  transition: transform 0.5s ease;
}
.img-zoom:hover {
  transform: scale(1.05);
}
.modal-backdrop {
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(5px);
}
.modal-content {
  background: linear-gradient(to bottom, #0e0e0e, #080808);
  border: 1px solid #2a2a2a;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.9);
}
.btn-approve {
  background: linear-gradient(to right, #38a169, #48bb78);
  color: white;
  transition: all 0.3s;
  box-shadow: 0 4px 6px -1px rgba(56, 161, 105, 0.2);
}
.btn-approve:hover {
  background: linear-gradient(to right, #48bb78, #68d391);
  box-shadow: 0 10px 15px -3px rgba(56, 161, 105, 0.3);
}
.btn-reject {
  background: linear-gradient(to right, #e53e3e, #f56565);
  color: white;
  transition: all 0.3s;
  box-shadow: 0 4px 6px -1px rgba(229, 62, 62, 0.2);
}
.btn-reject:hover {
  background: linear-gradient(to right, #f56565, #fc8181);
  box-shadow: 0 10px 15px -3px rgba(229, 62, 62, 0.3);
}
.document-card {
  transition: all 0.3s ease;
  border: 1px solid #222;
}
.document-card:hover {
  border-color: var(--color-primary);
  transform: translateY(-2px);
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.3);
}
.loading-spinner {
  border: 3px solid rgba(255, 255, 255, 0.1);
  border-top: 3px solid var(--color-primary);
  border-radius: 50%;
  width: 24px;
  height: 24px;
  animation: spin 1s linear infinite;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.search-field {
  background: rgba(26, 26, 26, 0.8);
  border: 1px solid #2a2a2a;
  transition: all 0.3s;
}
.search-field:focus {
  border-color: var(--color-primary);
  box-shadow: 0 0 0 2px rgba(255, 149, 14, 0.2);
  background: rgba(26, 26, 26, 1);
}
@media (max-width: 640px) {
  .mobile-stack {
    flex-direction: column;
  }
  .modal-actions {
    padding: 0.75rem;
  }
  .document-grid {
    grid-template-columns: 1fr;
  }
}
@keyframes pulse-slow {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}
.animate-pulse-slow {
  animation: pulse-slow 2.5s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  will-change: opacity;
}
.loading-shimmer {
  position: relative;
  overflow: hidden;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  animation: shimmer 1.5s infinite;
}
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}
.perspective {
  perspective: 1000px;
  transform-style: preserve-3d;
}
html {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
}
*:focus-visible {
  outline: 2px solid #ff950e;
  outline-offset: 2px;
  border-radius: 4px;
  box-shadow: 0 0 0 4px rgba(255, 149, 14, 0.1),
    0 0 0 2px rgba(255, 149, 14, 0.3);
  transition: box-shadow 0.2s ease;
}
*:focus:not(:focus-visible) {
  outline: none;
}
button:focus-visible, a:focus-visible {
  transform: scale(1.02);
  transition: transform 0.1s ease, box-shadow 0.2s ease;
}
@keyframes spin-slow {
  to {
    transform: rotate(360deg);
  }
}
.animate-spin-slow {
  animation: spin-slow 25s linear infinite;
  will-change: transform;
  transform-origin: center;
}
@keyframes spin-slow-reverse {
  to {
    transform: rotate(-360deg);
  }
}
.animate-spin-slow-reverse {
  animation: spin-slow-reverse 20s linear infinite;
  will-change: transform;
  transform-origin: center;
}
@keyframes spin-medium {
  to {
    transform: rotate(360deg);
  }
}
.animate-spin-medium {
  animation: spin-medium 35s linear infinite;
  will-change: transform;
  transform-origin: center;
}
@keyframes spin-medium-reverse {
  to {
    transform: rotate(-360deg);
  }
}
.animate-spin-medium-reverse {
  animation: spin-medium-reverse 30s linear infinite;
